
import tabGroup from '@/components/group/tab'
import bigAnniu from '@/components/group/biganniu'
import {forgetPassword} from '../../../api/user'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'

export default {
components: {
tabGroup: tabGroup,
bigAnniu: bigAnniu,
XInput,
Group,
Grid,
GridItem
},
data () {
return {
anniu: '下一步',
verCode: this.$route.params.verCode,
phone: this.$route.params.userName,
password: ''
}
},
props: {},
computed: {
},
methods: {
setsuccess () {
forgetPassword({
userName: this.phone,
verCode: this.verCode,
newPassword: this.password,
confPassword: this.password
}).then(res => {
console.log(res)
this.$router.push('/setsuccess')
}).catch(e => {
console.log(e)
})
},

clear () {
this.phone = ''
}
}
}



